@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@500;600;700&display=swap");
:root {
  --darkGray: hsl(0, 0%, 63%);
  --black: hsl(0, 0%, 0%);
  --white: hsl(0, 0%, 100%);
  --veryDarkGray: hsl(0, 0%, 27%);
}

:root {
  --font-Spartan: "Spartan", sans-serif;
  --font-normal: 500;
  --font-semiBold: 600;
  --font-Bold: 700;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-size: 0.75rem;
  line-height: 1.3;
  font-family: var(--font-Spartan);
  overflow-x: hidden;
  min-height: 100vh;
}
@media (max-width: 49.9375em) {
  body.no-scroll {
    overflow: hidden;
  }
}

a,
a:active,
a:visited {
  text-decoration: none;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4 {
  font-family: var(--font-Spartan);
}

.heading {
  color: var(--black);
}
.heading--xxl {
  font-size: clamp(1.6875rem, 5vw, 2.25rem);
  font-weight: var(--font-semiBold);
  line-height: 2.5625rem;
  margin-bottom: 0.75rem;
  transition: all 0.5s ease;
}
@media (min-width: 50em) {
  .heading--xxl {
    line-height: 2.875rem;
    margin-bottom: 1.4375rem;
  }
}
.heading--xl {
  font-size: 0.8125rem;
  font-weight: var(--font-Bold);
  letter-spacing: 0.3125rem;
  margin-bottom: 0.6875rem;
}

.paragraph {
  color: var(--darkGray);
  font-size: 0.75rem;
}
.paragraph--hero {
  line-height: 1.375rem;
}
.paragraph--about {
  line-height: 1.25rem;
}

.container {
  width: 100%;
  max-width: 65.75rem;
  margin: 0 auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  backdrop-filter: blur(0.125rem);
  transition: all 350ms ease-in-out;
}
@media (max-width: 49.9375em) {
  .overlay.overlay-open {
    visibility: visible;
    opacity: 1;
  }
}

.btn {
  position: relative;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
}
.btn--menu {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 2.4375rem;
  left: 1.875rem;
  z-index: 2;
}
@media (min-width: 50em) {
  .btn--menu {
    display: none;
  }
}
@media (max-width: 49.9375em) {
  .btn--menu {
    left: 1.3125rem;
  }
}
.btn--next, .btn--prev {
  background-color: var(--black);
  height: 4rem;
  width: 4rem;
  transition: all 0.5s;
}
.btn--next:focus, .btn--prev:focus {
  outline: none;
  transform: scale(1.05);
}
.btn--next:hover, .btn--prev:hover {
  background-color: var(--veryDarkGray);
}
.btn--next:focus-visible, .btn--prev:focus-visible {
  outline: 0.1875rem dashed var(--darkGray);
  outline-offset: 0.125rem;
}
@media (min-width: 50em) {
  .btn--next, .btn--prev {
    height: 5rem;
    width: 5rem;
  }
}
.btn--black {
  align-self: flex-start;
  margin-top: 1.25rem;
}
.btn--black:focus {
  outline: none;
}
.btn--black:focus-visible {
  outline: 3px dashed var(--black);
  outline-offset: 2px;
}
.btn--black:hover {
  opacity: 0.5;
}
.btn--black:hover .shopNow__text {
  margin-right: 1rem;
}
.btn--black .shopNow__text {
  font-size: 0.75rem;
  letter-spacing: 0.75rem;
  color: var(--black);
  line-height: 1.25rem;
  margin-right: 2rem;
  font-weight: var(--font-semiBold);
  transition: all 0.5s ease;
}

.header {
  position: absolute;
  left: 0;
  width: 100%;
  height: 6.25rem;
  z-index: 999;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 6.25rem;
  max-width: 78.125rem;
  background-color: transparent;
}
@media (min-width: 50em) {
  .header__container {
    justify-content: flex-start;
    padding: 4rem 1rem;
  }
}
.header__logo:focus {
  outline: none;
}
.header__logo:focus-visible {
  outline: 3px dashed var(--white);
  outline-offset: 2px;
}
.header__nav {
  position: absolute;
  width: 100%;
  top: -62.5rem;
  transition: all 350ms ease-in;
}
@media (min-width: 50em) {
  .header__nav {
    top: 0;
    background: transparent;
    position: initial;
    order: 1;
    width: 30%;
  }
}
@media (max-width: 49.9375em) {
  .header__nav.header__nav--active {
    top: 0;
    background-color: var(--white);
  }
}
.header__nav__link {
  position: relative;
  color: var(--black);
  font-family: var(--font-Spartan);
  font-weight: var(--font-semiBold);
}
.header__nav__link::before {
  content: "";
  position: absolute;
  bottom: -0.3125rem;
  right: 0;
  height: 0.125rem;
  width: 100%;
  background-color: var(--white);
  transform: scale(0);
  opacity: 0;
  visibility: hidden;
  transition: transform 350ms ease;
}
@media (min-width: 50em) {
  .header__nav__link {
    color: var(--white);
  }
  .header__nav__link:focus {
    outline: none;
  }
  .header__nav__link:focus-visible {
    outline: 3px dashed var(--white);
    outline-offset: 2px;
  }
  .header__nav__link:hover::before {
    opacity: 1;
    transform: scale(1);
    visibility: visible;
  }
}
.header__nav__list {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  list-style: none;
  height: 5.625rem;
}
@media (min-width: 50em) {
  .header__nav__list {
    justify-content: flex-start;
    margin-left: 3.25rem;
  }
}
.header__nav__list--item:not(:last-of-type) {
  margin-right: 1.9375rem;
}
@media (max-width: 49.9375em) {
  .header__nav__list--item:not(:last-of-type) {
    margin-right: 0.75rem;
  }
}

.hero {
  position: relative;
}
.hero__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 100%;
}
@media (min-width: 50em) {
  .hero__container {
    flex-direction: row;
  }
}
.hero__carousel {
  display: flex;
  position: relative;
}
@media (min-width: 50em) {
  .hero__carousel {
    flex: 1 0 50%;
  }
}
.hero__carousel__image {
  overflow: hidden;
  width: 100%;
  display: grid;
  grid-template-columns: max-content;
  height: 23rem;
}
.hero__carousel__image picture {
  grid-area: 1/2/1/2;
}
@media (min-width: 50em) {
  .hero__carousel__image {
    height: 60vh;
    min-height: 33rem;
  }
}
.hero__carousel__image--img {
  height: 23rem;
  width: 100%;
  object-fit: cover;
  opacity: 0;
  transform: scale(1.5);
  transition: all 1s;
}
@media (min-width: 50em) {
  .hero__carousel__image--img {
    height: 60vh;
    min-height: 33rem;
  }
}
.hero__carousel__image--img.active {
  display: block;
  opacity: 1;
  transform: scale(1);
  transition: all 1s;
}
.hero__carousel__button {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 50em) {
  .hero__carousel__button {
    right: -10rem;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: opacity 1s ease;
}
.hero__text--transition {
  opacity: 0;
}
@media (min-width: 50em) {
  .hero__text {
    padding: 5.8125rem 4vw 5.8125rem 6.7vw;
    flex: 1 0 27rem;
  }
}
@media (max-width: 49.9375em) {
  .hero__text {
    padding: 4.1875rem 2.1875rem;
  }
}

.about {
  position: relative;
}
.about__container {
  display: grid;
  max-width: 100%;
}
@media (min-width: 50em) {
  .about__container {
    grid-template-columns: 1fr 22.1875rem 1fr;
    grid-template-rows: 15.875rem;
  }
}
@media (min-width: 64em) {
  .about__container {
    grid-template-columns: 1fr 37.125rem 1fr;
    grid-template-rows: 16.5rem;
  }
}
.about__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.about__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2.4375rem 2.25rem;
}